// style.scss
.wp-block-buytiti-buytiti-bloque-categorias {
    display: flex;
    height: 330px;
    justify-content: center;
    margin-bottom: 15px !important;
    .imagen-contenedor {
        flex-grow: 1; 
        width: 215px;
        .contenedor-circular{
            display: flex;
            width: 120px; 
            height: 120px; 
            margin-top: 60px;
            border-radius: 50%;
            background-color: white;
            align-items: center;
            overflow: visible;
        }

        .imagenind-0 {
            margin: 10px;
            margin-left: 1px !important;
            object-fit: contain !important;
            transition: transform 0.7s ease; /* Añade una transición suave al efecto de zoom */
            position: relative; /* Añade esta línea */
        }
        .imagenind-1 {
            margin: 10px;
            margin-left: -5px !important;
            object-fit: contain !important;
            transition: transform 0.7s ease; /* Añade una transición suave al efecto de zoom */
            position: relative; /* Añade esta línea */
        }
        .imagenind-2 {
            margin-bottom: 40px !important;
            height: 130px;
            width: 120px;
            margin: 10px;
            object-fit: contain !important;
            transition: transform 0.7s ease; /* Añade una transición suave al efecto de zoom */
            position: relative; /* Añade esta línea */
        }
        .imagenind-3 {
            margin: 10px;
            object-fit: contain !important;
            transition: transform 0.7s ease; /* Añade una transición suave al efecto de zoom */
            position: relative; /* Añade esta línea */
            margin-left: -10px !important;
        }
        .imagenind-4 {
            object-fit: contain !important;
            transition: transform 0.7s ease !important;
            position: relative !important;
            width: 100px;
            height: 130px;
            margin-bottom: 23px !important;
        }
        border-radius: 27px; /* Ajusta este valor para cambiar cuánto quieres redondear las esquinas */
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 15px;  /* Ajusta este valor al tamaño que prefieras */

        .components-button {
            background-color: rgb(253, 161, 12);
            border-radius: 20px;
            margin-top: -8px;
            width: 85px;
            height: 25px;
            margin-bottom: 30px;
            color: white;
            font-size: 13px;
            font-weight: bold;
            text-align: center;
            text-decoration: none;
        }
        
        transition: transform 0.7s ease;
        /* Define el efecto al pasar el cursor */
        &:hover {
            .imagenind-0{
                transform-origin: bottom;
                transform: scale(1.8); 
                z-index: 2.6; /* Hace que la imagen sobresalga de los demás elementos */
            }
            .imagenind-1{
                transform-origin: bottom;
                transform: scale(1.8); 
                z-index: 2.6; /* Hace que la imagen sobresalga de los demás elementos */
            }
            .imagenind-2{
                transform-origin: bottom;
                transform: scale(1.6); 
                z-index: 2.6; /* Hace que la imagen sobresalga de los demás elementos */
            }
            .imagenind-3{
                transform-origin: bottom;
                transform: scale(2.1); 
                z-index: 2.6; /* Hace que la imagen sobresalga de los demás elementos */
            }
            .imagenind-4{
                transform-origin: bottom;
                transform: scale(1.8); 
                z-index: 2.6; /* Hace que la imagen sobresalga de los demás elementos */
            }
            /* Hace que el contenedor sea un poco más grande */
            transform: scale(1.1);
            z-index: 1;
        }
    }
}
